.component {
    align-items: flex-start;
    display: inline-flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
    width: 381px;
}

.component .image {
    height: 268px;
    margin-right: -45px;
    position: relative;
    width: 381px;
    object-fit: cover;
    border-radius: 8px;
    background: #FAFAFA;

    /* box-shadow */
    box-shadow: 1px 4px 44px 0px rgba(0, 0, 0, 0.03);
}

.component .details {
    align-items: flex-start;
    display: inline-flex;
    flex: 0 0 auto;
    position: relative;
    justify-content: space-between;
    width: 100%;
}

.component .name-and-categories {
    align-items: flex-start;
    display: inline-flex;
    flex: 0 0 auto;
    flex-direction: column;
    position: relative;
}

.component .title {
    color: var(--other-dark, #12151A);
    font-family: Poppins;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px; /* 142.857% */
    letter-spacing: 0.3px;
}

.component .categories {
    color: var(--other-gray-80, #B9B9B9);
    font-family: Poppins;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
    letter-spacing: 0.3px;
}

.component .price {
    color: #FFF;
    font-family: Poppins;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 16px; /* 133.333% */
    letter-spacing: 0.2px;
    border-radius: 5px;
    background: #192A67;
    display: flex;
    padding: 5px 10px;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.component .element {
    color: var(--primary-100);
    font-family: var(--small-bold-font-family);
    font-size: var(--small-bold-font-size);
    font-style: var(--small-bold-font-style);
    font-weight: var(--small-bold-font-weight);
    letter-spacing: var(--small-bold-letter-spacing);
    line-height: var(--small-bold-line-height);
    margin-top: -1px;
    position: relative;
    white-space: nowrap;
    width: fit-content;
}
