.body {
    margin-top: 123px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
}

#home-page-image {
    width: 728px;
    height: 474px;
    flex-shrink: 0;
    border-radius: 21px;
    object-fit: cover;
}

#left {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: left;
}

#title1 {
    color: #FFF;
    font-family: Poppins;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px; /* 133.333% */
    letter-spacing: 0.2px;
    background-color: #192A67;
    border-radius: 20px;
    padding: 5px 10px;
    margin: 0;
    width: fit-content;
}

#paragraph1 {
    color: var(--other-dark, #12151A);
    /* H1 */
    font-family: Poppins;
    font-size: 56px;
    font-style: normal;
    font-weight: 700;
    line-height: 72px; /* 128.571% */
    letter-spacing: 0.2px;
    width: 583px;
    margin: 0;
    margin-top: 10px;
    margin-bottom: 30px;
}

#search {
    display: flex;
    width: 513px;
}

#search-input {
    padding: 17px 20px;
    color: var(--secondary-90, #717184);
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 137.5% */
    letter-spacing: 0.3px;
    width: 513px;
    border-radius: 5px 0px 0px 5px;
    border: 0.5px solid var(--secondary-90, #717184);
    margin: 0;
}

#search-button {
    color: var(--primary-60, #E7F7FD);
    text-align: right;
    /* Paragraph Regular */
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 137.5% */
    letter-spacing: 0.3px;
    border-radius: 0px 5px 5px 0px;
    background: linear-gradient(180deg, #192A67 0%, #020438 100%);
    display: flex;
    width: 124px;
    padding: 17px 30px;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;
    cursor: pointer;
    border: none;
}

#paragraph2 {
    color: var(--other-gray-100, #656567);
    /* Paragraph Regular */
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 137.5% */
    letter-spacing: 0.3px;
    width: 430px;
    margin: 0;
    margin-top: 30px;
}


#categories {
    width: 23%;
    min-width: fit-content;
    max-width: 337px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: left;
}

#categories-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: left;
    margin: 0;
    width: 100%;
    padding: 0;
    gap: 0;
    margin-bottom: 30px;
    max-width: 337px;
}

.list {
    display: flex;
    align-items: center;
    gap: 15px;
    margin: 0;
    padding: 20px 19px;
    border: solid 1px #EBEBEB;
    width: 100%;
}

#title2 {
    border-radius: 10px 10px 0 0;
    background: linear-gradient(180deg, #020438 0%, #192A67 100%);
    padding: 20px;
    width: 100%;
    color: #FFF;
    font-family: Poppins;
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px; /* 123.077% */
    letter-spacing: 0.2px;
    margin: 0;
    max-width: 337px;
}

.last {
    border-radius: 0 0 10px 10px;
    border-top: none;
}

.margin {
    margin-top: 83px;
    align-items: flex-start;
    gap: 31px;
}

#newsletter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 91%;
    border-radius: 10px;
    background: rgba(40, 65, 132, 0.82);
    padding: 40px 30px;
    gap: 20px;
    max-width: 314px;
}

#input-container {
    width: 89%;
    display: flex;
    align-items: center;
    border-radius: 7px;
    border: 1px solid var(--devider-devider, #EBEBEB);
    background: #FFF;
    padding: 17px 20px;
    gap: 10px;
}

#input {
    border: none;
    background-color: transparent;
    color: var(--secondary-90, #717184);
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 137.5% */
    letter-spacing: 0.3px;
}

#title3 {
    color: #FFF;
    text-align: center;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; /* 133.333% */
    letter-spacing: 0.1px;
}

#paragraph3 {
    color: #000;
    text-align: center;
    font-family: Poppins;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
    letter-spacing: 0.3px;
}

#subscribe {
    width: 89%;
    min-width: fit-content;
    border: none;
    border-radius: 5px;
    background: linear-gradient(180deg, #192A67 0%, #020438 100%);
    color: #FFF;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 137.5% */
    letter-spacing: 0.3px;
    padding: 20px 30px;
}

#propositions {
    display: flex;
    flex-direction: column;
    width: 100%;
}

#top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 14px;
}

#title4 {
    color: var(--secondary-100, #33334F);
    font-family: Poppins;
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px; /* 123.077% */
    letter-spacing: 0.2px;
    width: max-content;
    margin: 0;
}

#all-categories {
    color: #192A67;
    text-align: right;
    font-family: Poppins;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px; /* 133.333% */
    letter-spacing: 0.2px;
    width: max-content;
}

#services {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 8px;
}

.component-1 {
    margin: 0;
    margin-bottom: 30px;
}


@media screen and (max-width : 1600px) {
    #services {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 8px;
    }
}

@media screen and (max-width : 1350px) {
    #home-page-image {
        width: 0;
        height: 0;
    }
}


@media screen and (max-width : 1200px) {
    #services {
        display: grid;
        grid-template-columns: 1fr;
        gap: 8px;
    }
}